
Template.body.addContent((function() {
  var view = this;
  return HTML.DIV({
    class: "container"
  }, HTML.Raw("\n        <h1>Presentations</h1>\n        "), HTML.UL("\n        ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("presentations"));
  }, function() {
    return [ "\n        ", Spacebars.include(view.lookupTemplate("presentation")), "\n        " ];
  }), "\n        "), "\n    ");
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("presentation");
Template["presentation"] = new Template("Template.presentation", (function() {
  var view = this;
  return HTML.LI(Blaze.View("lookup:text", function() {
    return Spacebars.mustache(view.lookup("text"));
  }));
}));
